<template>
  <div class="City">
    <appHerder :ifback='true' title='选择城市' go='/Address'></appHerder>
    <div class="search">
      <input type="text" v-model="search" placeholder="请输入搜索城市">
    </div>

    <div class="nowress"><i class="fa fa-send"></i>当前城市：{{address}}</div>
    <div class="area" ref="area"  v-if="searchCity.length == 0">
    <div class="cityinfo">
      <section class="hotcities citylist">
        <h4>&nbsp;热门城市</h4>
        <div>
          <div v-for="(value,index) in hotcities" :key="index" class="hciti" @click="setCity(value)">{{value.name}}</div>
        </div>
      </section>
      <section class="citys">
        <ul v-for="(value,index) in keys" :key="index" class="citylist">          
          <h3>{{value}}</h3>
          <li v-for="(city,num) in data[value]" :key="num" @click="setCity(city)">{{city.name}}</li>
        </ul>
      </section>
    </div>

    </div>
    <section class="setindex" v-if="searchCity.length == 0">
      <ul>
        <li @click="selectCity(0)">#</li>
        <li v-for="(value,index) in keys" :key="index" @click="selectCity(index+1)">{{value}}</li>
      </ul>
    </section>
      
    <div ref="searchCity" class="area">
    <div class="citys" >
      <ul class="citylist" v-if="searchCity.length != 0">
        <li v-for="(item,index) in searchCity" :key="index" @click="setCity(item)">{{item.name}}</li>
      </ul>
    </div></div>
  </div>
</template>

<script>
import Header from '../components/Header.vue'
import BScroll from 'better-scroll'
export default {
  name: 'City',
  data(){
  	return {
  		search:'',
      data:{},
      hotcities:{},
      keys:[],
      scroll:null,
      scroll2:null,
      allcity:[],
      searchCity:[]
  	}
  },
  computed:{
    address(){
      return this.city = this.$store.getters.location.addressComponent.city||
        this.$store.getters.location.addressComponent.province;
    }
  },
  components:{
  	appHerder:Header
  },
  created(){
  	this.getCityInfo();
  },
  mounted(){
    this.scroll = new BScroll(this.$refs.area,{
          click:true,
          tap:true
      });  
    this.scroll2 = new BScroll(this.$refs.searchCity,{
          click:true,
          tap:true
    });
  },
  watch:{
    search(e){
      // console.log(e);
      if(e == ''){
        this.searchCity = [];
      }else{
        this.searchCity = this.allcity.filter(item =>{
          return item.name.indexOf(e) != -1;
        })
      }
    }
  },
  methods:{
    getCityInfo(){
      this.$axios('/api/posts/cities')
      .then(res=>{
        this.data = res.data;
        this.keys = Object.keys(this.data);
        this.keys.pop();
        this.keys.sort();
        this.hotcities = this.data.hotCities;
        // console.log(this.hotcities);
        this.keys.forEach(item=>{
          this.data[item].forEach(value=>{
            // console.log(value.name);
            this.allcity.push(value);
          })
        })

      }).catch(err=>{
        console.log(err);
      })
    },
    selectCity(index){
      let obj = document.getElementsByClassName('citylist')[index];
      // console.log(obj);
      this.scroll.scrollToElement(obj,1000);
    },
    setCity(city){
      this.$store.dispatch('setCity',city.name+'市');
      this.$router.push('/Address');
    }
  }
}
</script>


<style scoped>
.City{
  width: 100%;
  height: 100%;
}
.search{
  width: 100%;
  height:2rem;
  background-color: #fff;
  line-height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search input{
  width: 95%;
  height: 1.4rem;
  line-height: 1.4rem;
  border:none;
  box-sizing: border-box;
  background-color: #e3e3e3;
  padding-left: 1em;
  font-size: 0.6rem;
  border-radius: 10px;
}
.nowress{
  margin-top: 4px;
  background-color: #fff;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
}
.nowress i{
  color: blue;
  margin: 0 7px;
}
.hotcities{
  background-color: #fff;
  margin-top: 5px;
}
.hotcities>div{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  
}
.hotcities .hciti{
  width: 35%;
  text-align: center;
  height: 30px;
  line-height: 30px;
  background-color: #e3e3e3;
  margin: 5px 0;
  border-radius: 5px;
}
.citys{
  background-color: #fff;
  margin-top: 2px;
  padding: 0 5px;
}
.citys ul li{
  list-style: none;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
}
.setindex{
  position: fixed;
  right: 5px;
  top: 25%;
}
.setindex ul li{
  list-style: none;
}
.area{
  height: calc(100% - 144px);
  overflow: hidden;
}
</style>